<template>
  <div class="search">
    <div class="tadcitem">
      <div class="m-hmsrch">
        <div class="top">
          <span class="iconfont back" @click="$emit('srech-back')"
            >&#xe6db;</span
          >
          <form action="#" class="m-input">
            <div class="inputcover">
              <i class="srch"></i>
              <input
                type="text"
                class="input"
                v-model="value"
                @focus="searchsuccesongs = null"
                @keyup.enter="
                  getSearchSucceSong(value);
                  $event.target.blur();
                "
              />
              <label v-show="!value"></label>
              <div class="close">
                <i
                  class="clo"
                  @click="
                    value = '';
                    searchsuccesongs = null;
                  "
                  v-show="value"
                ></i>
              </div>
            </div>
          </form>
        </div>
        <section class="loader" v-if="loading">
          <div class="loading">
            <img src="../assets/loading.gif" alt="" />
          </div>
        </section>
        <div class="default">
          <section class="hotlis" v-if="!value && !loading">
            <h3 class="title"></h3>
            <ul class="list">
              <HotSerchSong
                v-for="(hotsearchsong, i) in hotsearchsongs"
                :key="i"
                :hotsearchsong="hotsearchsong"
                @search-succes-song="
                  getSearchSucceSong(hotsearchsong.first);
                  value = hotsearchsong.first;
                "
              />
            </ul>
          </section>
          <section class="history" v-if="!value && !loading">
            <ul class="list">
              <HistorySerchSong
                v-for="(historyserchsong, i) in historyserchsong"
                :key="i"
                :historyserchsong="historyserchsong"
                @search-history-song="
                  getSearchSucceSong(historyserchsong);
                  value = historyserchsong;
                "
                @close-history-song="deleHistorySong(historyserchsong)"
              />
            </ul>
          </section>

          <section class="serach" v-if="value && !searchsuccesongs && !loading">
            <h3 class="ser-title">"{{ value }}"</h3>
            <ul>
              <GetSerachSong
                v-for="(searchsong, i) in searchsongs"
                :key="i"
                :searchsong="searchsong"
                @searchsucces-song="
                  getSearchSucceSong(searchsong.keyword);
                  value = searchsong.keyword;
                "
              />
            </ul>
          </section>
          <section class="searchartists" v-if="searchsuccesongs">
            <h3 class="serbest">"</h3>
            <div>
              <GetSearchSinger
                :searchartists="searchartists"
                :searchalbums="searchalbums"
                :searchplaylists="searchplaylists"
              />
            </div>
          </section>
          <section class="serachsuccess" v-if="searchsuccesongs">
            <!-- <h3 class="ser-title">"{{ value }}"</h3> -->
            <ul>
              <GetSearchSucceSong
                v-for="(searchsuccesong, i) in searchsuccesongs?.songs"
                :key="i"
                :searchsuccesong="searchsuccesong"
                @music-play="$emit('music-play', $event)"
                @music-play-list="
                  $emit('music-play-list', searchsuccesongs?.songs)
                "
              />
            </ul>
          </section>
        </div>
      </div>
    </div>

    <!-- <HotSong v-for="hotsong in hotsongs" :key="hotsong.id" :hotsong="hotsong" /> -->
  </div>
</template>

<script>
import HotSerchSong from "@/components/HotSerchSong.vue";
import GetSerachSong from "@/components/GetSerachSong.vue";
import GetSearchSucceSong from "@/components/GetSearchSucceSong.vue";
import HistorySerchSong from "@/components/HistorySerchSong.vue";
import GetSearchSinger from "@/components/GetSearchSinger.vue";
var _ = require("lodash");

export default {
  components: {
    HotSerchSong,
    GetSerachSong,
    GetSearchSucceSong,
    HistorySerchSong,
    GetSearchSinger,
  },
  data: function () {
    return {
      hotsearchsongs: [],
      value: "",
      searchsongs: [],
      searchsuccesongs: null,

      searchartists: null,
      searchalbums: null,
      searchplaylists: null,

      loading: false,
      historyserchsong:
        JSON.parse(window.localStorage.getItem("historyserchsong")) || [],
    };
  },
  methods: {
    getSearchSong: function (keywords) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: { keywords, type: "mobile" },
        })
        .then((res) => {
          // console.log(res);
          this.value
            ? (this.searchsongs = res.data.result.allMatch)
            : (this.searchsongs = []);
          this.loading = false;
        });
    },
    getSearchSucceSong: function (keywords) {
      this.loading = true;

      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
          // params: { keywords, limit: 10, offset: 0 },
          params: { keywords },
        }),
        this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
          params: { keywords },
        }),
      ]).then(([song, art]) => {
        // console.log(song, art);
        this.searchsuccesongs = song.data.result;

        this.searchartists = art.data.result.artist;
        this.searchalbums = art.data.result.album
          ? art.data.result.album
          : null;
        this.searchplaylists = art.data.result.playlist
          ? art.data.result.playlist
          : null;
        this.loading = false;
      });
      this.historyserchsong = keywords
        ? [...new Set([keywords, ...this.historyserchsong])]
        : [...this.historyserchsong];
      if (this.historyserchsong.length > 10) {
        this.historyserchsong = this.historyserchsong.slice(0, 10);
      }
    },
    deleHistorySong: function (historyserchsong) {
      // console.log(historyserchsong);
      this.historyserchsong = this.historyserchsong.filter(
        (o) => o !== historyserchsong
      );
    },
  },
  created() {
    this.loading = true;

    this.axios.get("https://apis.netstart.cn/music//search/hot").then((res) => {
      this.hotsearchsongs = res.data.result.hots;
      this.loading = false;
    });
    this.debouncedGetSearchsong = _.debounce(this.getSearchSong, 800);
  },
  watch: {
    value: function (newValue) {
      // console.log(key);
      if (newValue) {
        this.debouncedGetSearchsong(newValue);
      } else {
        this.searchsongs = [];
      }
    },
    historyserchsong: function (newHistoryserchsong) {
      localStorage.setItem(
        "historyserchsong",
        JSON.stringify(newHistoryserchsong)
      );
    },
  },
  // mounted() {
  //   this.$router.push("/");
  // },
};
</script>

